.firstSection {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100vh;
}

.firstSection .leftSection {
  font-size: 3rem;
  line-height: 10vh;
  min-width: 50%;
  text-align: center;
}

@media only screen and (max-width: 1200px) {
  .firstSection {
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    height: 80vh;
  }

  .firstSection .rightSection {
    display: none;
  }

  .firstSection .leftSection {
    width: 100%;
    align-self: center;
  }
}

#intoduction {
  font-size: 18px;
  line-height: 26px;
  width: 82%;
  margin-top: 30px;
  font-weight: 100;
  margin: auto;
}

.firstSection .leftSection #name {
  color: rgb(109, 56, 126);
}

.tag-list {
  width: 30rem;
  max-width: 90vw;
  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  gap: 1rem 0;
  position: relative;
  padding: 1.5rem 0;
  overflow: hidden;
  margin: auto;
}

.loop-slider {
  .inner {
    display: flex;
    width: fit-content;
    animation-name: loop;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: var(--direction);
    animation-duration: var(--duration);
  }
}

.tag {
  display: flex;
  align-items: center;
  gap: 0 0.2rem;
  color: #e2e8f0;
  font-size: 0.9rem;
  background-color: #334155;
  border-radius: 0.4rem;
  padding: 0.7rem 1rem;
  margin-right: 1rem;
  box-shadow: 0 0.1rem 0.2rem rgb(0 0 0 / 20%), 0 0.1rem 0.5rem rgb(0 0 0 / 30%),
    0 0.2rem 1.5rem rgb(0 0 0 / 40%);

  span {
    font-size: 1.2rem;
    color: #64748b;
  }
}

.fade {
  pointer-events: none;
  background: linear-gradient(
    90deg,
    #1e293b,
    transparent 30%,
    transparent 70%,
    #1e293b
  );
  position: absolute;
  inset: 0;
}

@keyframes loop {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.btn-custom {
  border: none;
  background-color: #22272b;
  color: white;
  font-size: 18px;
  margin-right: 23px;
  padding: 8px;
}

.btn-custom:hover {
  background-color: #38414a;
}

.btn-custom a {
  text-decoration: none;
  color: white;
}

a .btn-custom:hover{
  color:white;  
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  height: 40vh;
  margin-top: 30px;
}

.grid-item img {
  height: 14vh;
}

.grid-item img:hover {
  border: 1px solid grey;
  opacity: 0.3;
}

.orbit {
  width: 100%;
  min-width: 100vw;
  min-height: 100vh;
}
.orbit-icon {
  width: 1.7em;
  height: 1.7em;
  line-height: 1.6em;
  font-size: 1.2em;
  border-radius: 50%;
  background: #ccc;
  color: #fff;
  text-align: center;
  display: block;
}
.orbit-wrap {
  height: 40em;
  list-style: none;
  font-size: 1.3em;
}
.orbit-wrap > li {
  position: absolute;
  left: 74%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.orbit-wrap > li:hover ul {
  border-width: 2px;
  border-color: #fff;
}
.orbit-wrap > li:hover ~ li ul {
  border-color: rgba(255, 255, 255, 0.2);
}
.orbit-wrap > li:hover ~ li ul li {
  opacity: 0.4;
}
ul[class^="ring"] {
  transition: all 300ms ease-in-out;
}
ul[class^="ring"] li {
  transition: all 300ms ease-in-out;
}
.ring-0 {
  width: 25em;
  height: 25em;
  animation: clockwiseRotate 35s linear infinite;
}
.ring-0 i {
  animation: counterClockwiseRotate 35s linear infinite;
}
.ring-0 > *:nth-of-type(1) {
  transform: rotate(95deg) translate(12.5em) rotate(-95deg);
}
.ring-0 > *:nth-of-type(2) {
  transform: rotate(190deg) translate(12.5em) rotate(-190deg);
}
.ring-0 > *:nth-of-type(3) {
  transform: rotate(285deg) translate(12.5em) rotate(-285deg);
}
.ring-0 > *:nth-of-type(4) {
  transform: rotate(380deg) translate(12.5em) rotate(-380deg);
}
.ring-1 {
  width: 20em;
  height: 20em;
  animation: clockwiseRotate 30s linear infinite;
}
.ring-1 i {
  animation: counterClockwiseRotate 30s linear infinite;
}
.ring-1 > *:nth-of-type(1) {
  transform: rotate(126.6666666667deg) translate(10em)
    rotate(-126.6666666667deg);
}
.ring-1 > *:nth-of-type(2) {
  transform: rotate(253.3333333333deg) translate(10em)
    rotate(-253.3333333333deg);
}
.ring-1 > *:nth-of-type(3) {
  transform: rotate(380deg) translate(10em) rotate(-380deg);
}
.ring-2 {
  width: 15em;
  height: 15em;
  animation: clockwiseRotate 25s linear infinite;
}
.ring-2 i {
  animation: counterClockwiseRotate 25s linear infinite;
}
.ring-2 > *:nth-of-type(1) {
  transform: rotate(47.5deg) translate(7.5em) rotate(-47.5deg);
}
.ring-2 > *:nth-of-type(2) {
  transform: rotate(95deg) translate(7.5em) rotate(-95deg);
}
.ring-2 > *:nth-of-type(3) {
  transform: rotate(142.5deg) translate(7.5em) rotate(-142.5deg);
}
.ring-2 > *:nth-of-type(4) {
  transform: rotate(190deg) translate(7.5em) rotate(-190deg);
}
.ring-2 > *:nth-of-type(5) {
  transform: rotate(237.5deg) translate(7.5em) rotate(-237.5deg);
}
.ring-2 > *:nth-of-type(6) {
  transform: rotate(285deg) translate(7.5em) rotate(-285deg);
}
.ring-2 > *:nth-of-type(7) {
  transform: rotate(332.5deg) translate(7.5em) rotate(-332.5deg);
}
.ring-2 > *:nth-of-type(8) {
  transform: rotate(380deg) translate(7.5em) rotate(-380deg);
}
.ring-3 {
  width: 10em;
  height: 10em;
  animation: clockwiseRotate 20s linear infinite;
}
.ring-3 i {
  animation: counterClockwiseRotate 20s linear infinite;
}
.ring-3 > *:nth-of-type(1) {
  transform: rotate(126.6666666667deg) translate(5em) rotate(-126.6666666667deg);
}
.ring-3 > *:nth-of-type(2) {
  transform: rotate(253.3333333333deg) translate(5em) rotate(-253.3333333333deg);
}
.ring-3 > *:nth-of-type(3) {
  transform: rotate(380deg) translate(5em) rotate(-380deg);
}
ul[class^="ring"] {
  border: solid 1px rgba(33, 150, 243, 0.8);
  position: relative;
  padding: 0;
  border-radius: 50%;
  list-style: none;
  box-sizing: content-box;
}
ul[class^="ring"] li {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1.6em;
  height: 1.6em;
  margin: -0.8em;
}
/* center;
*/
.orbit-center {
  z-index: 5;
  font-size: 2em;
  width: 2em;
  height: 2em;
  line-height: 1.8em;
  text-align: center;
  background: hotpink;
  border-radius: 50%;
}
.orbit-center:hover .orbit-center__icon {
  transform: rotateZ(0deg);
}
.orbit-center__icon {
  transform: rotateZ(-360deg);
  transition: all 300ms ease-in-out;
}
.orbit-wrap > li.orbit-center:hover ~ li > ul {
  width: 0;
  height: 0;
}
.orbit-wrap > li.orbit-center:hover ~ li > ul * {
  transform: translate(0, 0);
}
/* animations */
@keyframes clockwiseRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes counterClockwiseRotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

.orbit img {
  height: 6vh;
}